.tablerow__cells{width:100%;display:grid;grid-template-columns:25% 13% 13% 5% 5% 5% 20% 14%;padding:20px 0}.tablerow__cells>div:nth-child(2){margin:0 auto;cursor:pointer}.tablerow__cells>div:nth-child(4),.tablerow__cells>div:nth-child(5),.tablerow__cells>div:nth-child(6){margin:0 auto}.tablerow__cells>div:last-child{justify-self:flex-end}.tablerow__cells ._bold{font-weight:700}.tablerow__cells ._price{margin-left:70px}.tablerow._paid .tablerow__cells{grid-template-columns:25% 13% 17% 10% 5% 5% 5% 10% 10%}.tablerow._paid .tablerow__cells>div:nth-child(4){margin:0}.tablerow._paid .tablerow__cells>div:nth-child(5),.tablerow._paid .tablerow__cells>div:nth-child(6),.tablerow._paid .tablerow__cells>div:nth-child(7),.tablerow._paid .tablerow__cells>div:nth-child(8){margin:0 auto}.tablerow__cells{align-items:center;border-bottom:1px solid #d7e2e4;transition:all .4s}.tablerow__cells>div:first-child{display:flex;align-items:center;column-gap:15px;cursor:pointer}.tablerow__cells>div:first-child::before{content:"";width:17px;height:6px;background:center/contain url("~assets/images/accordion.svg") no-repeat;transition:all .5s}.tablerow._active .tablerow__cells{border-color:#fff}.tablerow._active .tablerow__cells>div:first-child::before{transform:rotate3d(1, 0, 0, 180deg)}.tablerow__cell._link{position:relative;transition:all .4s}.tablerow__cell._link:hover{transform:scale(1.1)}.tablerow__cell._link::before{content:"Скопировано";font-size:14px;line-height:1;background-color:#f9f9fb;padding:3px 6px;position:absolute;bottom:120%;left:calc(50% - 47px);opacity:0;transition:all .4s;pointer-events:none}.tablerow__cell._link._copied::before{opacity:1}.tablerow__additional{display:flex;column-gap:50px;overflow:hidden;height:0;background-color:#f9f9fb;border-radius:10px;padding:0 30px;transition:all .4s;box-sizing:content-box}.tablerow__additional-container._searchphrases{max-width:285px}.tablerow__additional-title{font-size:14px;margin-bottom:20px;line-height:17px}.tablerow__additional-text{font-weight:600;margin-top:7px}.tablerow._paid .tablerow__status{display:flex;column-gap:3px}.tablerow._paid .tablerow__status span{width:12px;height:9px;background-color:#f9f9fb;border-radius:3px}.tablerow._paid .tablerow__status span._active{background-color:#37ac38}.tablerow._paid .tablerow__status span._active:has(+span:not(._active)){animation:stateping 1.3s infinite}@keyframes stateping{50%{background-color:#f9f9fb}}.tablerow__iconbuttons{display:flex;align-items:center;column-gap:35px}.tablerow__iconbutton{width:31px;height:31px;background:center/contain no-repeat;transition:all .5s}.tablerow__iconbutton:hover{transform:rotate(180deg)}.tablerow__iconbutton._refresh{background-image:url(~assets/images/refreshicon.svg)}.tablerow__iconbutton._cog{background-image:url(~assets/images/cogicon.svg)}.tablerow__iconbutton._delete{background-image:url(~assets/images/cross.svg);width:20px;height:20px}